.voice-bot-container {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  padding: 40px;
  gap: 40px;
  font-family: "Arial", sans-serif;
  color: #fff;
  
  min-height: 90vh;
}

.voice-bot-left {
  flex: 1;
}

.voice-bot-left h1 {
  font-size: 2.5rem;
  margin-bottom: 10px;
  color: #00bfff;
}

.voice-bot-left p {
  font-size: 1.1rem;
  line-height: 1.6;
  margin-bottom: 20px;
  color: #dfe6e9;
}

.voice-buttons {
  margin-bottom: 20px;
}

.voice-buttons button {
  padding: 12px 24px;
  font-size: 16px;
  border-radius: 8px;
  border: none;
  cursor: pointer;
  margin-right: 15px;
  transition: 0.3s;
}

.start-listen-btn {
  background-color: #27ae60;
  color: white;
}

.start-listen-btn:hover {
  background-color: #2ecc71;
}

.stop-listen-btn {
  background-color: #e74c3c;
  color: white;
}

.stop-listen-btn:hover {
  background-color: #ff4c4c;
}

.stop-speak-btn {
  background-color: #f39c12;
  color: white;
}

.stop-speak-btn:hover {
  background-color: #ffa502;
}

.voice-textbox {
  background-color: #333;
  padding: 15px 20px;
  border-radius: 12px;
  margin-bottom: 15px;
  font-size: 16px;
}

.voice-textbox.bot-text {
  background-color: #444;
}

.voice-textbox p {
  margin: 5px 0 0 0;
}

.voice-bot-right {
  display: flex;
  
      /* centers image vertically if container has height */
  padding: 10px;


  
  max-width: 200px; /* optional, limits container width */
   margin-right: 400px;
}

.voice-bot-right img {
  width: 500px;       /* image fills container width */
  height: 300px;      /* maintain aspect ratio */
   /* makes image circular, remove if you want square */
  object-fit: cover;  /* ensures image covers the area nicely */
 
 
}
